@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom Blog Content Styling */
.prose-content {
  @apply text-gray-800;
}

.prose-content h2 {
  @apply text-3xl font-bold text-gray-900 mt-8 mb-4 border-b-2 border-amber-600 pb-2;
}

.prose-content h3 {
  @apply text-2xl font-bold text-gray-900 mt-6 mb-3;
}

.prose-content p {
  @apply text-gray-700 leading-relaxed mb-4;
}

.prose-content a {
  @apply text-cyan-600 hover:text-cyan-700 font-semibold underline decoration-2 underline-offset-2;
}

.prose-content ul {
  @apply list-disc list-inside space-y-2 mb-4 text-gray-700;
}

.prose-content ol {
  @apply list-decimal list-inside space-y-2 mb-4 text-gray-700;
}

.prose-content strong {
  @apply font-bold text-gray-900;
}

.prose-content em {
  @apply italic;
}

.prose-content code {
  @apply bg-gray-100 text-red-600 px-2 py-1 rounded text-sm font-mono;
}

.prose-content pre {
  @apply bg-gray-900 text-gray-100 p-4 rounded-lg overflow-x-auto mb-4;
}

.prose-content blockquote {
  @apply border-l-4 border-cyan-600 pl-4 italic text-gray-600 mb-4;
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Custom scrollbar for webkit browsers */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #06B6D4;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #0891B2;
}
